import Card from "../UI/Card";
import MealItem from "./MealItem/MealItem";
import classes from "./AvailableMeals.module.css";

const DUMMY_MEALS = [
  {
    id: "m1",
    name: "Bún Bò Huế",
    description: "Full topping",
    price: 35000,
  },
  {
    id: "m2",
    name: "Bún Vịt",
    description: "Vịt và măng tươi!",
    price: 30000,
  },
  {
    id: "m3",
    name: "Phở Bò",
    description: "Full topping, kèm rau",
    price: 30000,
  },
  {
    id: "m4",
    name: "Nem Nướng",
    description: "Ngon bổ rẻ",
    price: 30000,
  },
];

const AvailableMeals = () => {
  const mealsList = DUMMY_MEALS.map((meal) => (
    <MealItem
      key={meal.id}
      id={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default AvailableMeals;
